export const HttpClientHeaders = {
  httpPostSettingsWithAccessToken: (body: any, accessToken: string): RequestInit => {
    if (body instanceof FormData) {
      return {
        method: 'POST',
        headers: {
          Accept: 'application/json',
          Authorization: 'Bearer ' + accessToken,
        },
        body: body
      }
    }
    else {
      return {
        method: 'POST',
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + accessToken,
        },
        body: JSON.stringify(body)
      }
    }
  },
  httpPutSettingsWithAccessToken: (body: any, accessToken: string): RequestInit => {
    if (body instanceof FormData) {
      return {
        method: 'PUT',
        headers: {
          Accept: 'application/json',
          Authorization: 'Bearer ' + accessToken,
        },
        body: body
      }
    }
    else {
      return {
        method: 'PUT',
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + accessToken,
        },
        body: JSON.stringify(body)
      }
    }
  },
  httpDeleteSettingsWithAccessToken: (accessToken: string) => {
    return {
      method: 'DELETE',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
        Authorization: 'Bearer ' + accessToken,
      },
    }
  },
  httpPostSettings: (body: any) => {
    return {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(body)
    }
  },
  httpPutSettings: (body: any) => {
    return {
      method: 'PUT',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(body)
    }
  },
  httpDeleteSettings: (body: any) => {
    return {
      method: 'DELETE',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(body)
    }
  },
  httpGetHeaders: {
    method: 'GET',
    headers: {
      'Response-Type': 'application/json',
    }
  },
  httpGetHeadersWithAccessToken: (accessToken: string): RequestInit => {
    return {
      method: 'GET',
      mode: 'cors',
      credentials: 'include',
      headers: {
        'Response-Type': 'application/json',
        'Authorization': 'Bearer ' + accessToken
      }
    }
  }
};
