import { IPublicClientApplication, SilentRequest } from '@azure/msal-browser';
import { protectedResources } from '../constants';
import { ApiQueryModel } from '../Models/Models';
import { HttpClientHeaders } from './HttpClientHeaders';

export const ApiClient = {
  Config: {
    baseUrl: `${protectedResources.apiWeatherForecast.endpoint}/WeatherForecast`,
    //baseUrl: 'https://localhost:5000/WeatherForecast', 
    errorMessage: 'Invalid response ',
  },
  get: (instance: IPublicClientApplication): Promise<ApiQueryModel> => {
    return new Promise((resolve, reject) => {
      const silentRequest: SilentRequest = {
        scopes: protectedResources.apiWeatherForecast.scopes,
        account: instance.getActiveAccount()!
      };

      const url = ApiClient.Config.baseUrl;
      // Silently acquires an access token which is then attached to a request for api calls
      instance.acquireTokenSilent(silentRequest).then((token) => {
        // Use access to in call to protected api
        // console.log(token.accessToken);
        // console.log(token.idToken);
        fetch(url, HttpClientHeaders.httpGetHeadersWithAccessToken(token.accessToken)).then(response => {
          if (response.ok) {
            resolve(response.json());
          }
          else {
            reject(response.statusText);
          }
        }).catch(error => {
          reject(error);
        });
      }).catch((e) => {
        instance.loginRedirect(silentRequest).then(() => {
          // Use access to in call to protected api
          instance.acquireTokenSilent(silentRequest).then((token) => {
            // Use access to in call to protected api
            fetch(url, HttpClientHeaders.httpGetHeadersWithAccessToken(token.accessToken)).then(response => {
              resolve(response.json());
            }).catch(error => {
              reject(error);
            });
          }).catch(error => {
            console.log(ApiClient.Config.errorMessage + error);
            reject(error);
          });
        }).catch(error => {
          reject(error);
        });;
      });
    });
  },
};